// @flow
import React from 'react'
import styles from './Title.css'
import { withRouter } from 'react-router'

const switchTitle = (path: string) => {
  if (path.startsWith('/operator')) return '运营商'
  if (path.startsWith('/store')) return '店铺'
  if (path.startsWith('/user-center')) return '主页'
  if (path.startsWith('/supplement')) return '补货'
  if (path.startsWith('/login')) return '登录'
  if (path.startsWith('/devices')) return '设备列表'
  if (path.startsWith('/stockouts')) return '缺货汇总'
  if (path.startsWith('/replenish')) return '库存管理'
  return ''
}

const hideNames = [ '主页', '补货', '设备列表', '缺货汇总', '库存管理' ]

class Title extends React.Component<{match: Object}, {}> {
  componentWillReceiveProps (nextProps) {
    const { match: { path } } = nextProps
    document.title = switchTitle(path)
  }
  render () {
    const { match: { path } } = this.props
    const title = switchTitle(path)
    const hideDetail = hideNames.includes(title)
    return (
      <div>
        {
          hideDetail
          ? false
          : (
            <div>
              <img src='./VEM@3x.png' alt='cover' style={{ width: '100vw' }}/>
              {
                title === '登录'
                ? <div className={styles['login-tips']} style={{ backgroundImage: 'url(./logo_y.png)' }}/>
                : <div className={styles.tips}>选择{title}</div>
              }

            </div>
          )
        }
      </div>
    )
  }
}
export default withRouter(Title)



