// @flow
import React from 'react'
import { withRouter } from 'react-router'
import Title from 'components/Title'
import { connect } from 'react-redux'
import styles from './Devices.css'
import { Icon } from 'antd-mobile'
import { uniFetch } from 'store/modules/fetch'

const ASSET_URL = process.env.ASSET_URL || 'http://api.billion.dev.kelvins.io/rmb-asset/'

type State = {
  devices: Array<Object>,
}
type Props = {
  operator: Object,
  store: Object,
  history: Object,
}

class Devices extends React.PureComponent<Props, State> {
  constructor () {
    super()
    this.state = {
      devices: [],
    }
  }

  componentDidMount () {
    this.fetchAssets()
  }

  fetchAssets () {
    const { operator, store, history } = this.props
    const oid = operator.id
    const sid = store.id
    if (sid === undefined) {
      history.push('/')
      return
    }
    const url = `${ASSET_URL}stores/assets`
    const data = { headers: { oid, sid } }
    this.postData(url, data)
  }

  async postData (url: string, data: Object) {
    try {
      const res = await uniFetch(url, data)
      const json = await res.json()
      this.setState({ devices: json.result })
    } catch (erro) {
      console.error(erro)
    }
  }

  render () {
    const { devices } = this.state
    const { operator, store, history } = this.props
    return (
      <div>
        <Title />
        <div className={styles.container}>
          <div className={styles.header}>
            <div className={styles['header-title']}>
              <p>运营商:&ensp;{operator.name}</p>
              <p>店&ensp;&ensp;铺:&ensp;{store.name}</p>
            </div>
          </div>
          {
            devices.map(item => (
              <div className={styles['device-card']} key={item.id}>
                <div className={styles['left-top']}>
                  <div className={styles['title-wraper']}>
                    <span className={styles['device-name']}>{item.name}&ensp;{item.eqNumber}&ensp;</span>
                    {
                      item.onlineFlag
                      ? (<span className={styles['on-line']}>在线</span>)
                      : (<span className={styles['off-line']}>离线</span>
                      )
                    }
                  </div>
                  <p className={styles['store-info']}>{store.name}</p>
                  <p className={styles['store-info']}>{store.address}</p>
                </div>
                <div
                  className={styles['right-top']}
                  onClick={() => { history.push('/replenish', item) }}
                >
                补货
                </div>
                <div className={styles['left-bottom']}>
                  <p className={styles['supplement-info']}>断货数: 24</p>
                  <p className={styles['supplement-info']}>断货率: 65%</p>
                </div>
                <div
                  className={styles['right-bottom']}
                >
                缺货汇总 <Icon type="right" size="md" />
                </div>
              </div>
            ))
          }
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  operator: state.userInfo.operator,
  store: state.userInfo.store,
})
export default withRouter(connect(mapStateToProps, {})(Devices))



