// @flow
import React from 'react'
import { uniFetch } from 'store/modules/fetch'
import { withRouter } from 'react-router'
import Title from 'components/Title'
import styles from './Store.css'
import { SearchBar } from 'antd-mobile'
import { connect } from 'react-redux'
import { setStore } from 'store/modules/userInfo'

const STORE_URL = process.env.STORE_URL || 'http://api.billion.dev.kelvins.io/rmb-store/'
type State = {
  storeMenu: Array<Object>,
  showSearchBar: boolean,
  storeFiler: ?string,
}
type Props = {
  history: Object,
  location: Object,
  setStore: Function,
  oid: number,
}

class Store extends React.PureComponent<Props, State> {
  handleSaveStore: Function
  constructor () {
    super()
    this.state = {
      storeMenu: [],
      showSearchBar: false,
      storeFiler: undefined,
    }
    this.handleSaveStore = this.handleSaveStore.bind(this)
  }

  componentDidMount () {
    this.fetchStores()
  }

  async fetchStores () {
    const { oid } = this.props
    if (oid === undefined) {
      this.props.history.push('/')
      return
    }
    const url = `${STORE_URL}users/stores?pageNo=-1`
    const data = { headers: { oid } }
    try {
      const res = await uniFetch(url, data)
      const json = await res.json()
      const storeMenu = json.page.result
      const showSearchBar = storeMenu.length > 10
      this.setState({ storeMenu, showSearchBar })
    } catch (erro) {
      console.error(erro)
    }
  }

  handleSaveStore (store: Object) {
    const { history } = this.props
    this.props.setStore(store)
    history.push('/user-center')
  }
  render () {
    const { showSearchBar, storeFiler } = this.state
    let { storeMenu } = this.state
    if (storeFiler) {
      storeMenu = storeMenu.filter(item => item.name.includes(storeFiler))
    }
    return (
      <div className={styles.container}>
        <Title />
        {
          showSearchBar
          ? (
            <div style={{ paddingTop: 10 }} >
              <SearchBar
                placeholder="搜索店铺"
                onChange={(value) => { this.setState({ storeFiler: value }) }}
              />
            </div>
          )
          : false
        }
            <div>
              {
                storeMenu.map(item => (
                  <div
                    key={item.id}
                    className={styles.bar}
                    onClick={() => { this.handleSaveStore(item) }}
                  >
                  <img src='./dianpu@3x.png' alt='icon' className={styles.icon}/>
                  <div className={styles['operator-name']}>
                    <p>{item.name}</p>
                    <p className={styles.address}>{item.address}</p>
                  </div>
                  <div className={styles['store-status']}>
                    {
                      item.storeStatus === 3
                      ? <span style={{ color: '#ffba00' }}>营业中</span>
                    : <span style={{ color: '#999999' }}>已打烊</span>
                    }
                  </div>
                  </div>
                ))
              }
            </div>
      </div>
    )
  }
}

const mapDispatchToProps = {
  setStore,
}

const mapStateToProps = (state) => ({
  oid: state.userInfo.operator.id,
})
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Store))



