// @flow
import React from 'react'
import { withRouter } from 'react-router'
import Title from 'components/Title'
import { connect } from 'react-redux'
import styles from './Supplement.css'
import { uniFetch } from 'store/modules/fetch'

type State = {
  devicesNum: number,
}
type Props = {
  operator: Object,
  store: Object,
  history: Object,
}

const ASSET_URL = process.env.ASSET_URL || 'http://api.billion.dev.kelvins.io/rmb-asset/'

class Supplement extends React.PureComponent<Props, State> {
  constructor () {
    super()
    this.state = {
      devicesNum: 0,
    }
  }
  componentDidMount () {
    this.fetchAssets()
  }

  fetchAssets () {
    const { operator, store, history } = this.props
    const oid = operator.id
    const sid = store.id
    if (sid === undefined) { history.push('/') }
    const url = `${ASSET_URL}stores/assets`
    const data = { headers: { oid, sid } }
    this.postData(url, data)
  }

  async postData (url: string, data: Object) {
    try {
      const res = await uniFetch(url, data)
      const json = await res.json()
      this.setState({ devicesNum: json.result.length })
    } catch (erro) {
      console.error(erro)
    }
  }

  render () {
    const { operator, store, history } = this.props
    const { devicesNum } = this.state
    return (
      <div>
        <Title />
        <div className={styles.container}>
          <div className={styles.header}>
            <div className={styles['header-title']}>
              <p>运营商:&ensp;{operator.name}</p>
              <p>店&ensp;&ensp;铺:&ensp;{store.name}</p>
            </div>
            <img
              src='./home@3x.png'
              className={styles.home}
              onClick={() => { history.push('/user-center') }}
            />
          </div>
          <div className={`${styles.header} detail`}>
            <span className={styles['detail-name']}>设备数量&ensp;(台)&ensp;:&ensp;&ensp;</span>
            <span className={styles['detail-num']}>{devicesNum}</span>
          </div>
          <div className={`${styles.header} detail`}>
            <span className={styles['detail-name']}>缺货设备&ensp;(台)&ensp;:&ensp;&ensp;</span>
            <span className={styles['detail-num']}>3</span>
          </div>
          <div className={`${styles.header} detail`}>
            <span className={styles['detail-name']}>缺货商品&ensp;(个)&ensp;:&ensp;&ensp;</span>
            <span className={styles['detail-num']}>3</span>
          </div>
          <div className={styles['btn-wraper']}>
            <div className={styles.btn} onClick={() => { history.push('/devices') }}>设备列表</div>
            <div className={styles.btn} onClick={() => { history.push('/stockouts') }}>缺货汇总</div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  operator: state.userInfo.operator,
  store: state.userInfo.store,
})
export default withRouter(connect(mapStateToProps, {})(Supplement))



