// @flow
import React from 'react'
import Switch from 'react-router-dom/Switch'
import Route from 'react-router-dom/Route'
import routes from '../../routes'

// normalize with antd and add icons & animations
import 'antd/lib/style/css'
import 'antd-mobile/dist/antd-mobile.css'
import '../../styles/core.css'
import styles from './CoreLayout.css'

export const CoreLayout = () => (
  <div className={styles['core-layout']}>
    <div className={styles['viewport']}>
        <Switch>
        <Route path='/' component={routes.operator} exact />
        <Route path='/login' component={routes.login} exact />
        <Route path='/operator' component={routes.operator} exact />
        <Route path='/store' component={routes.store} exact />
        <Route path='/user-center' component={routes.userCenter} exact />
        <Route path='/supplement' component={routes.supplement} exact />
        <Route path='/devices' component={routes.devices} exact />
        <Route path='/stockouts' component={routes.stockouts} exact />
        <Route path='/replenish' component={routes.replenish} exact />
      </Switch>
    </div>
  </div>
)

export default CoreLayout



