// @flow
import React from 'react'
import 'whatwg-fetch'
import { uniFetch } from '../../store/modules/fetch'

const UC2B_URL = process.env.UC2B_URL || 'http://api.billion.dev.kelvins.io/rmb-uc2b/'
const STORE_URL = process.env.STORE_URL || 'http://api.billion.dev.kelvins.io/rmb-store/'
const ASSET_URL = process.env.ASSET_URL || 'http://api.billion.dev.kelvins.io/rmb-asset/'
const INVENTORY_URL = process.env.INVENTORY_URL || 'http://api.billion.dev.kelvins.io/rmb-inventory/'
const ITEM_URL = process.env.ITEM_URL || 'http://api.billion.dev.kelvins.io/rmb-item/'

class Replenishment extends React.PureComponent<{}, {}> {
  componentDidMount () {
    // this.fetchOrganizations()
  }

  fetchOrganizations () {
    // 需要加分页
    const url = `${UC2B_URL}selves/organizations?pageNo=1`
    this.postData(url, {})
  }

  fetchStores (oid: string) {
    // 需要加分页
    const url = `${STORE_URL}stores?pageNo=1&pageSize=100`
    const data = {
      headers: {
        oid,
      },
    }
    this.postData(url, data)
  }

  fetchAssets (oid: string, sid: string) {
    const url = `${ASSET_URL}stores/assets`
    const data = {
      headers: {
        oid,
        sid,
      },
    }
    this.postData(url, data)
  }

  fetchItems (storeId: string) {
    const [ oid, sid ] = [ '4', '3' ]
    const url = `${ITEM_URL}stores/${storeId}/all_items`
    const data = {
      headers: {
        oid,
        sid,
      },
    }
    this.postData(url, data)
  }

  fetchRacks (id: string) {
    const oid = '4'
    const sid = '3'
    const url = `${ASSET_URL}eq_assets/${id}/racks`
    const data = {
      headers: {
        oid,
        sid,
      },
    }
    this.postData(url, data)
  }

  handleInventory (oid: string, sid: string) {
    const rackInfo = {
      eqAssetId: '62778',
    }
    const { eqAssetId } = rackInfo
    const url = `${INVENTORY_URL}stores/assets`
    const body = {
      detail: {
        itemId: 1, // 商品id
        quantity: 1, // 数量
        rackId: 1, // 货道id
      },
      eqAssetId,
      orderId: 10000,
      orgId: oid,
      storeId: sid,
    }
    const data = {
      headers: {
        oid,
        sid,
      },
    }
    this.postData(url, data)
  }

  async postData (url: string, data: Object) {
    try {
      const res = await uniFetch(url, data)
      if (res.status === 400) {
        console.error('未知错误')
        return
      }
      const json = await res.json()
      console.log(json)
    } catch (erro) {
      console.error(erro)
    }
  }
  render () {
    return (
      <div>
        <div onClick={() => { this.fetchOrganizations() }}>获取所有运营商</div>
        <div onClick={() => { this.fetchStores('4') }}>获取获取商店</div>
        <div onClick={() => { this.fetchAssets('4', '3') }}>获取商店设备</div>
        <div onClick={() => { this.fetchItems('3') }}>获取所有商店下所有商品</div>
        <div onClick={() => { this.fetchRacks('62778') }}>获取货道</div>
        <div onClick={() => { this.handleInventory('4', '73276') }}>入库</div>
      </div>

    )
  }
}

export default Replenishment



