// @flow
import React from 'react'
import { Button, Toast, InputItem } from 'antd-mobile'
import 'whatwg-fetch'
import { withRouter } from 'react-router'
import styles from './Login.css'

type State = {
  login: ?string,
  password: ?string,
}

type Props = {
  history: Object,
  setOid: Function,
}

const UC2B_URL = process.env.UC2B_URL || 'http://api.billion.dev.kelvins.io/rmb-uc2b/'

class Login extends React.PureComponent<Props, State> {
  fetchUserInfo: Function
  constructor () {
    super()
    this.state = {
      login: undefined,
      password: undefined,
    }
    this.fetchUserInfo = this.fetchUserInfo.bind(this)
  }
  fetchUserInfo () {
    const { login, password } = this.state
    if (!login || !password) {
      Toast.fail('账号或密码不能为空', 1)
      return
    }
    const url = `${UC2B_URL}login`
    const body = JSON.stringify({ 'phoneNo': login, 'password': password })
    const data = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json;charset=UTF-8',
      },
      body,
    }
    this.postData(url, data)
  }

  async postData (url: string, data: Object) {
    try {
      const res = await fetch(url, data)
      if (res.status === 400) {
        Toast.fail('账号或密码错误', 1)
        return
      }
      const json = await res.json()
      localStorage.setItem('uc2bToken', json.result.token)
      this.props.history.push('/operator')
    } catch (erro) {
      console.error(erro)
    }
  }

  render () {
    const { login, password } = this.state
    return (
      <div className={styles.container}>
        <InputItem type='number' value={login} placeholder='手机号'
          onChange={value => { this.setState({ login: value }) }}
        >
        <img src='./user@3x.png' alt='userName' />
        </InputItem>
        <InputItem type='password' value={password} placeholder='密码'
          onChange={value => { this.setState({ password: value }) }}
        >
          <img src='./password@3x.png' alt='userName' />
        </InputItem>
        <div className={styles.login} onClick={this.fetchUserInfo}>登录</div>
      </div>

    )
  }
}

export default withRouter(Login)



