// @flow
import React from 'react'
import { withRouter } from 'react-router'
import Title from 'components/Title'
import { connect } from 'react-redux'
import styles from './Stockouts.css'
import { Icon } from 'antd-mobile'
import { uniFetch } from 'store/modules/fetch'

const ASSET_URL = process.env.ASSET_URL || 'http://api.billion.dev.kelvins.io/rmb-asset/'

type State = {
  devices: Array<Object>,
}
type Props = {
  operator: Object,
  store: Object,
  history: Object,
}

class Stockouts extends React.PureComponent<Props, State> {
  constructor () {
    super()
    this.state = {
      devices: [],
    }
  }

  componentDidMount () {
    this.fetchAssets()
  }

  fetchAssets () {
    const { operator, store } = this.props
    const oid = operator.id
    const sid = store.id
    const url = `${ASSET_URL}stores/assets`
    const data = { headers: { oid, sid } }
    this.postData(url, data)
  }

  async postData (url: string, data: Object) {
    try {
      const res = await uniFetch(url, data)
      const json = await res.json()
      this.setState({ devices: json.result })
    } catch (erro) {
      console.error(erro)
    }
  }

  render () {
    const { operator, store } = this.props
    return (
      <div>
        <Title />
        <div className={styles.container}>
          <div className={styles.header}>
            <div className={styles['header-title']}>
              <p>运营商:&ensp;{operator.name}</p>
              <p>店&ensp;&ensp;铺:&ensp;{store.name}</p>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  operator: state.userInfo.operator,
  store: state.userInfo.store,
})
export default withRouter(connect(mapStateToProps, {})(Stockouts))



