// @flow
import React from 'react'
import styles from './CommodityCard.css'
import { Icon, Stepper, Button, Modal } from 'antd-mobile'

const alert = Modal.alert

type State = {
  showJustify: boolean,
  quantity: number,
}

type Props = {
  onRackChange: Function,
  commodityInfo: Object,
  onDeleteCommodity: Function,
  onNumChange: Function,
  onDelete: Function,
}

class CommodityCard extends React.PureComponent<Props, State> {
  handleQuantityChange: Function
  handleDelete: Function
  constructor (props: Props) {
    super(props)
    this.state = {
      showJustify: false,
      quantity: props.commodityInfo.quantity,
    }
    this.handleQuantityChange = this.handleQuantityChange.bind(this)
    this.handleDelete = this.handleDelete.bind(this)
  }

  handleQuantityChange (value: number) {
    const commodityInfo = { ...this.props.commodityInfo }
    if (value === commodityInfo.quantity) {
      this.props.onDeleteCommodity(commodityInfo.rackId)
    } else {
      commodityInfo.quantity = value
      this.props.onNumChange({ ...commodityInfo })
    }
    this.setState({ quantity: value })
  }

  handleDelete () {
    const { commodityInfo } = this.props
    alert('删除商品', '确认删除吗?', [
    { text: '取消', onPress: () => {} },
    { text: '确认', onPress: () => this.props.onDelete(commodityInfo) },
    ])
  }

  componentWillReceiveProps (nextProps: Props) {
    const { quantity } = nextProps.commodityInfo
    if (quantity !== this.props.commodityInfo.quantity) {
      this.setState({ quantity })
    }
  }

  render () {
    const { showJustify, quantity } = this.state
    const { commodityInfo } = this.props
    const hasCommodity = commodityInfo.itemId > 0
    return (
      <div className={styles.container}>
        <div className={styles.commodity}>
          <div className={styles['commodity-main']}>
            <div>
              <div
                style={{ backgroundImage: `url(${commodityInfo.itemImageUrl || './default-commodity@3x.png'})` }}
                className={styles.cover}/>
              <div className={styles['commodity-des']}>
                <div className={styles['commodity-title']}>
                  <p>{commodityInfo.quantity}</p>
                  <p>{commodityInfo.itemName || '暂无商品'}</p>
                </div>
                <p
                  className={showJustify ? styles['show-justify'] : styles.justify }
                  onClick={() => { this.setState({ showJustify: !showJustify }) }}
                >
                调整商品<Icon type="down" size="md"/>
                </p>
              </div>
            </div>
            <div className={styles.stepper}>
              <Stepper
              style={{ width: '100%', minWidth: '100px' }}
              showNumber
              max={hasCommodity ? commodityInfo.capacity : 0}
              min={hasCommodity ? commodityInfo.quantity : 0}
              value={quantity}
              onChange={this.handleQuantityChange}
              disabled={!hasCommodity}
            />
            </div>
          </div>
          <div className={styles.btns} style={showJustify ? { height: 50, paddingTop: 20 } : {}}>
            <Button type="primary" size="small" inline
              onClick={() => this.props.onRackChange(commodityInfo)}
            >换货
            </Button>
            <Button type="primary" size="small" inline
              onClick={this.handleDelete}
              disabled={!hasCommodity}
            >删除
            </Button>
            <Button type="primary" size="small" inline disabled>减货</Button>
          </div>
        </div>
      </div>

    )
  }
}
export default CommodityCard



