// @flow
import React from 'react'
import { withRouter } from 'react-router'
import Title from 'components/Title'
import { connect } from 'react-redux'
import styles from './UserCenter.css'
import { Modal } from 'antd-mobile'

const alert = Modal.alert
type State = {
}
type Props = {
  operator: Object,
  store: Object,
  history: Object,
  match: Object,
}

const LinkMenu = [
  {
    link: '/supplement',
    label: '补货',
    imgSrc: './buhuo@3x.png',
  },
  {
    link: '/',
    label: '运营数据',
    imgSrc: './yunyingshuju@3x.png',
  },
  {
    link: '/',
    label: '故障处理',
    imgSrc: './guzhangchuli@3x.png',
  },
  {
    link: '/',
    label: '退款审核',
    imgSrc: './tuikuanshenhe@3x.png',
  },
]

class UserCenter extends React.PureComponent<Props, State> {
  handleLogOut: Function
  constructor () {
    super()
    this.state = {
    }
    this.handleLogOut = this.handleLogOut.bind(this)
  }

  componentWillMount () {
    const { store, history } = this.props
    if (store.id === undefined) { history.push('/') }
  }

  componentDidMount () {
    document.title = '主页'
  }

  handleLogOut () {
    alert('退出', '确认退出吗?', [
      { text: '取消', onPress: () => console.log('cancel') },
      { text: '确认', onPress: () => this.logOut() },
    ])
  }

  logOut () {
    const { history } = this.props
    localStorage.removeItem('uc2bToken')
    history.push('/login')
  }

  render () {
    const { operator, store, history } = this.props
    return (
      <div>
        <Title />
        <div className={styles.container}>
          <div className={styles.header}>
            <div className={styles['header-title']}>
              <p>运营商:&ensp;{operator.name}</p>
              <p>店&ensp;&ensp;铺:&ensp;{store.name}</p>
            </div>
            <img src='./logout@3x.png' style={{ height: 20 }} className={styles.logout} onClick={this.handleLogOut}/>
          </div>
          <div className={styles['link-wraper']}>
            {
              LinkMenu.map((item, index) => (
                <div
                  key={item.label}
                  className={styles['link-box-wraper']}
                  style={(index % 2) === 0 ? { marginRight: 20 } : {}}
                  onClick={() => { history.push(item.link) }}
                >
                  <div className={styles['link-box']}>
                    <div className={styles['link-content']}>
                      <img src={item.imgSrc} style={{ height: 80 }}/>
                      <p className={styles['link-des']}>{item.label}</p>
                    </div>
                  </div>
                </div>
              ))
            }
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  operator: state.userInfo.operator,
  store: state.userInfo.store,
})
export default withRouter(connect(mapStateToProps, {})(UserCenter))



