// @flow
export const SAVE_OID = 'SAVE_OID'
export const SAVE_STORE = 'SAVE_STORE'

// ------------------------------------
// Actions
// ------------------------------------
export function setOperator (value: Object) {
  return {
    type: SAVE_OID,
    payload: value,
  }
}

export function setStore (value: Object) {
  return {
    type: SAVE_STORE,
    payload: value,
  }
}

export const actions = {
  setOperator,
  setStore,
}
const ACTION_HANDLERS = {
  [SAVE_OID]: (state, action) => Object.assign({}, state, {
    operator: action.payload,
  }),
  [SAVE_STORE]: (state, action) => Object.assign({}, state, {
    store: action.payload,
  }),
}

// ------------------------------------
// Reducer
// ------------------------------------
const initialState = {
  operator: {},
  store: {},
}
export default function counterReducer (state: Object = initialState, action: Object) {
  const handler = ACTION_HANDLERS[action.type]

  return handler ? handler(state, action) : state
}



