// @flow
import React from 'react'
import { Button } from 'antd-mobile'
import { uniFetch } from 'store/modules/fetch'
import { withRouter } from 'react-router'
import Title from 'components/Title'
import { connect } from 'react-redux'
import { setOperator } from 'store/modules/userInfo'

import styles from './Operator.css'

const UC2B_URL = process.env.UC2B_URL || 'http://api.billion.dev.kelvins.io/rmb-uc2b/'

type State = {
  operatorMenu: Array<Object>,
}
type Props = {
  history: Object,
  setOperator: Function,
}
class Operator extends React.PureComponent<Props, State> {
  handleSetOperator: Function
  constructor () {
    super()
    this.state = {
      operatorMenu: [],
    }
    this.handleSetOperator = this.handleSetOperator.bind(this)
  }

  componentDidMount () {
    this.fetchOrganizations()
  }

  async fetchOrganizations () {
    const url = `${UC2B_URL}selves/organizations?pageNo=1&pageSize=100`
    try {
      const res = await uniFetch(url, {})
      const json = await res.json()
      this.setState({ operatorMenu: json.page.result })
    } catch (erro) {
      console.error(erro)
    }
  }

  handleSetOperator (operator: Object) {
    const { history } = this.props
    this.props.setOperator(operator)
    // sessionStorage.setItem('operator', JSON.stringify(operator))
    history.push('/store')
  }

  render () {
    const { operatorMenu } = this.state
    return (
      <div>
        <Title />
          <div>
            {
              operatorMenu.map(item => (
                <div
                  key={item.id}
                  className={styles.bar}
                  onClick={() => this.handleSetOperator(item)}
                >
                <img src='./renwu@3x.png' alt='icon' className={styles.icon}/>
                <span className={styles['operator-name']}>{item.name}</span>
                </div>
              ))
            }
          </div>
      </div>
    )
  }
}
const mapDispatchToProps = {
  setOperator,
}

const mapStateToProps = (state) => ({
})
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Operator))



