// @flow
import React from 'react'
import styles from './ReplacingCard.css'
import { Modal } from 'antd-mobile'

const alert = Modal.alert

type State = {
}

type Props = {
  commodityInfo: Object,
  onItemChange: Function,
}

class ReplacingCard extends React.PureComponent<Props, State> {
  handleChangeItem: Function
  constructor (props: Props) {
    super(props)
    this.state = {
    }
    this.handleChangeItem = this.handleChangeItem.bind(this)
  }

  handleChangeItem () {
    const { commodityInfo, onItemChange } = this.props

    alert('更换商品', `确认更换当前商品为"${commodityInfo.title}?"`, [
    { text: '取消', onPress: () => {} },
    { text: '确认', onPress: () => onItemChange(commodityInfo) },
    ])
  }

  render () {
    const { commodityInfo = {} } = this.props
    return (
      <div className={styles.container} onClick={this.handleChangeItem}>
        <div className={styles.commodity}>
          <div className={styles['commodity-main']}>
            <div>
              <div
                style={{ backgroundImage: `url(${commodityInfo.imageUrl || './default-commodity@3x.png'})` }}
                className={styles.cover}/>
              <div className={styles['commodity-des']}>
                <div className={styles['commodity-title']}>
                  <p>{commodityInfo.title}</p>
                  <p className={styles['commodity-id']}>{commodityInfo.itemCode}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    )
  }
}
export default ReplacingCard



