// @flow
import React from 'react'
import { withRouter } from 'react-router'
import Title from 'components/Title'
import { connect } from 'react-redux'
import styles from './Replenish.css'
import { uniFetch } from 'store/modules/fetch'
import CommodityCard from 'components/CommodityCard'
import ReplacingCard from 'components/ReplacingCard'
import { Toast, SearchBar, Modal } from 'antd-mobile'

const alert = Modal.alert

const INVENTORY_URL = process.env.INVENTORY_URL || 'http://api.billion.dev.kelvins.io/rmb-inventory/'
const ITEM_URL = process.env.ITEM_URL || 'http://api.billion.dev.kelvins.io/rmb-item/'
type State = {
  commodityList: Array<Object>,
  goods: Array<Object>,
  showOperator: boolean,
  currentRack: ?number,
  goodsFilter: ?string,
}
type Props = {
  operator: Object,
  store: Object,
  history: Object,
  location: Object,
}

class Replenish extends React.PureComponent<Props, State> {
  commodityListCopy: Array<Object>
  changedList: Array<Object>
  handleCommodityChange: Function
  handleDeleteCommodity: Function
  postChanges: Function
  handleDelete: Function
  handleGoodsChange: Function
  handleChangeRack: Function
  handleCancel: Function
  onceReplenish: Function
  constructor () {
    super()
    this.state = {
      commodityList: [ ],
      showOperator: false,
      goods: [],
      currentRack: undefined,
      goodsFilter: undefined,
    }
    this.commodityListCopy = []
    this.changedList = []
    this.handleCommodityChange = this.handleCommodityChange.bind(this)
    this.handleDeleteCommodity = this.handleDeleteCommodity.bind(this)
    this.postChanges = this.postChanges.bind(this)
    this.handleDelete = this.handleDelete.bind(this)
    this.handleGoodsChange = this.handleGoodsChange.bind(this)
    this.handleChangeRack = this.handleChangeRack.bind(this)
    this.handleCancel = this.handleCancel.bind(this)
    this.onceReplenish = this.onceReplenish.bind(this)
  }

  componentDidMount () {
    const { store: { id }, history } = this.props
    if (id === undefined) {
      history.push('/')
      return
    }
    this.fetchInventory()
    this.fetchItems()
  }

  fetchInventory () {
    const { operator, store, location: { state } } = this.props
    const oid = operator.id
    const sid = store.id
    const url = `${INVENTORY_URL}eq_assets/${state.id}/inventories?page=1&pageSize=100`
    const data = { headers: { oid, sid } }
    this.postData(url, data)
  }

  async postData (url: string, data: Object) {
    try {
      const res = await uniFetch(url, data)
      const json = await res.json()
      const commodityList = json.page.result
      this.setState({ commodityList, showOperator: false, currentRack: undefined })
      this.changedList = []
      this.commodityListCopy = commodityList
    } catch (erro) {
      console.error(erro)
    }
  }

  handleDeleteCommodity (rackId: number) {
    const { changedList } = this
    const index = changedList.findIndex(item => item.rackId === rackId)
    if (index !== -1) {
      changedList.splice(index, 1)
      this.setState({ showOperator: changedList.length > 0 })
    }
  }

  handleCommodityChange (value: Object) {
    const { changedList } = this
    const index = changedList.findIndex(item => item.rackId === value.rackId)
    if (index === -1) {
      changedList.push(value)
    } else {
      changedList[index] = value
    }
    this.setState({ showOperator: changedList.length > 0 })
  }

  postChanges () {
    const { changedList } = this
    const details = changedList.map(item => {
      const oldCommodity = this.commodityListCopy.find(old => old.rackId === item.rackId)
      const oldQuantity = oldCommodity.quantity || 0
      return ({
        itemId: item.itemId, // 商品id
        quantity: item.quantity - oldQuantity, // 数量
        rackId: item.rackId, // 货道id
      })
    })
    Toast.success('已提交', 1)
    this.postRacks(details)
  }

  async postRacks (details: Array<Object>) {
    const url = `${INVENTORY_URL}inventories/entries`
    const { operator, store } = this.props
    const oid = operator.id
    const sid = store.id
    const body = JSON.stringify({
      details,
      eqAssetId: this.props.location.state.id,
      orderId: 10000,
      orgId: oid,
      storeId: sid,
    })
    const data = {
      method: 'POST',
      headers: { oid, sid, 'Content-Type': 'application/json', 'Accept': 'application/json' },
      body,
    }
    await uniFetch(url, data)
    this.fetchInventory()
  }

  async handleDelete (commodityInfo: Object) {
    const url = `${INVENTORY_URL}racks/${commodityInfo.rackId}/items`
    const { operator, store } = this.props
    const oid = operator.id
    const sid = store.id
    const data = {
      method: 'DELETE',
      headers: { oid, sid, 'Content-Type': 'application/json', 'Accept': 'application/json' },
    }
    await uniFetch(url, data)
    Toast.success('删除成功', 1)
    this.fetchInventory()
  }

  async fetchItems () {
    const { operator, store } = this.props
    const oid = operator.id
    const sid = store.id
    const url = `${ITEM_URL}stores/${sid}/all_items`
    const data = {
      method: 'GET',
      headers: { oid, sid, 'Content-Type': 'application/json', 'Accept': 'application/json' },
    }
    const res = await uniFetch(url, data)
    const json = await res.json()
    this.setState({ goods: json.result })
  }

  async handleGoodsChange (goodsData: Object) {
    const { commodityListCopy } = this
    const { currentRack } = this.state
    const rack = commodityListCopy.find(item => item.rackId === currentRack) || {}
    rack.itemId = goodsData.id
    rack.quantity = 0
    this.postRacks([ rack ])
  }

  handleChangeRack (rackInfo: Object) {
    this.setState({ currentRack: rackInfo.rackId })
  }

  handleCancel () {
    // 暂时未重置已修改数字,来不及了,啊啊啊....
    // this.changedList = []
    this.setState({ commodityList: [ ...this.commodityListCopy ], showOperator: false })
  }

  onceReplenish () {
    alert('一键补货?', '', [
    { text: '取消', onPress: () => {} },
    { text: '确认', onPress: () => { this.replenishAll() } },
    ])
  }

  replenishAll () {
    const commodityList = this.state.commodityList.filter(item => item.itemId > 0)
    const details = commodityList.map(item => {
      return ({
        itemId: item.itemId, // 商品id
        quantity: item.capacity - item.quantity, // 数量
        rackId: item.rackId, // 货道id
      })
    })
    this.postRacks(details)
    Toast.success('补货完成', 1)
  }

  render () {
    const { location: { state } } = this.props
    const { goodsFilter, commodityList, showOperator, currentRack } = this.state
    let { goods } = this.state
    if (goodsFilter) {
      goods = goods.filter(item => (
        item.title.includes(goodsFilter)
      ))
    }
    return (
      <div>
        <Title />
        {
          currentRack
          ? (
            <div className={styles.container}>
               <SearchBar
                 placeholder="Search"
                 value={goodsFilter}
                 onChange={(value) => { this.setState({ goodsFilter: value }) }}
                 />
              {
                goods.map(item => (
                  <ReplacingCard key={item.id}
                    commodityInfo={item}
                    onItemChange={this.handleGoodsChange}
                    />
                ))
              }
            </div>
          )
          : (
            <div className={styles.container}>
              <div className={styles.title}>
                <span className={styles['device-name']}>{state.name}&ensp;({state.model})&ensp;</span>
                <div className={styles['once-replenish']}
                  onClick={this.onceReplenish}
                >一键补货
                </div>
              </div>
              <div style={{ paddingBottom: 120 }}>
                {
                  commodityList.map(item => (
                    <CommodityCard
                      key={item.rackId} commodityInfo={item}
                      onDeleteCommodity={this.handleDeleteCommodity}
                      onNumChange={this.handleCommodityChange}
                      onDelete={this.handleDelete}
                      onRackChange={this.handleChangeRack}
                      />
                  ))
                }
              </div>
              <div className={styles.btns} style={showOperator ? { bottom: 0 } : {}}>
                <div className={styles.cancel}
                  onClick={this.handleCancel}
                >取消
                </div>
                <div className={styles.confirm} onClick={this.postChanges}>确定</div>
              </div>
            </div>
          )
        }
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  operator: state.userInfo.operator,
  store: state.userInfo.store,
})
export default withRouter(connect(mapStateToProps, {})(Replenish))



