// @flow
import 'whatwg-fetch'
import debug from './debug'
import update from 'immutability-helper'

type Params = {
  path: string,
  options: Object
}

function authorization (params: Params): Promise<*> {
  const token = localStorage.getItem('uc2bToken')
  if (!token) {
    window.location.href = '/login'
  }
  let newParams
  if (token) {
    newParams = update(params, {
      options: {
        headers: {
          $set: Object.assign({}, params.options.headers, {
            'x-auth-token': `${token}`,
          }),
        },
      },
    })
  } else {
    newParams = params
  }
  return Promise.resolve(newParams)
}

function handleFetch ({ path, options }: Params): Promise<*> {
  return fetch(path, options)
}

export function handleFetchError (e: Error) {
  debug.error(e)
}

function isUnauthorized ({ status }: Object) {
  return status === 401
}

function handleUnauthorized (response) {
  if (isUnauthorized(response)) {
    // FIXME: use redux
    localStorage.setItem('uc2bToken', '')
    window.location.href = '/login'
  }

  return response
}

function handleError (response) {
  if (!response.ok) {
    return response.json().then((err) => {
      if (err.code === 401001) {   // token过期
        window.location.href = '/login'
        throw err
      }
      throw err
    })
  }

  return response
}

export function uniFetchNA (path: string, options: Object = {}): Promise<*> {
  const params = { path, options }
  return handleFetch(params)
    .then(handleError)
}

export function uniFetch (path: string, options: Object = {}): Promise<*> {
  const params = { path, options }
  return authorization(params)
    .then(handleFetch)
    .then(handleUnauthorized)
    .then(handleError)
}



